.App {
  text-align: center;
}

.value {
  font-size: 50px;
  font-weight: bold;
  color: #61dafb;
  margin-bottom: 20px;
}

.btn {
  display: inline-block;
  margin: 10px auto;
  padding: 10px 20px;
  color: #fff;
  background: linear-gradient(250deg, #43a7f9,#8cffdb);
  border: none;
  border-radius: 5px;
}

.btn button {
  cursor: pointer;
  font-size: 50px;
  margin: 30px;
  border: none;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}